$f_primary: 'Roboto';
body{
  font-family: $f_primary !important;
}
.header{
  position: fixed !important;
  left: 0;
  top: 0;
  width: 100%;
  background: linear-gradient(180deg, #000000 0%, #212121 100%) !important;
  box-shadow: 0px 0px 40px rgba(0, 255, 115, 0.4);
  padding: 10px 110px !important;
  z-index: 99999 !important;
  .desc_linl_pnt{
    margin: 0 auto;
  }
  .header_inn{
    align-items: center;
    display: contents;
  }
  .link_prnt{
    display: flex;
    align-items: center;
   
    a{
      font-family: $f_primary;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #ffffff;
      background: transparent;
      box-shadow: none;
      margin-right:55px;
      transition: all 0.2s ease-out;
      cursor: pointer;
      &:hover, &.active{
        color: #78dda5;
      }
    }
    .last_link{
      margin-right: 0;
    }
  }
  .enterapp_prnt{
    .enterapp_btn{
      width: 147px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #00FF73;
      border-radius: 60px;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: #353535;
      border: 1px solid #00FF73;
      transition: all 0.2s ease-out;
      &:hover{
        background: #079145;
        color: #FFFFFF;
      }
    }
  }
}
@media screen and (max-width: 1600px) {
  .header {
    padding: 10px 60px !important;
  }
  .header .link_prnt a {
    margin-right: 25px;
  }
}
@media screen and (max-width: 1199px) {
  .header {
    padding: 10px 30px !important;
  }
  .header .link_prnt a {
    margin-right: 23px;
    font-size: 13px;

  }
}
@media screen and (min-width: 992px) {
  .mobl_link_prnt{
    display: none;
  }
}
@media screen and (max-width: 991px) {
  .header .link_prnt a {
    margin-right: 0px;    
  }
  .link_prnt{
    flex-direction: column;
    padding: 5px;
    padding-bottom: 18px;
    a{
      margin-top: 15px;
      margin-right: 0;
    }
    .last_link{
      margin-bottom: 15px;
    }
  }
  .header .nav_callpse{
    position: absolute;
    left: 0;
    top: 89px;
    background: #20301af7;
    width: 100%;
  }
  // .header .collpse_btn {
  //   border: 2px solid #00FF73;
  //   box-shadow: none;
  //   background: transparent ;
  //   svg{
  //     color: #00FF73;
  //   }
  // }
  .header {
    .collpse_boder {
      border: 2px solid #00FF73;
      box-shadow: none;
      background: transparent;
      &:focus{
        box-shadow: none;
      }  
      svg{
          color: #00FF73;
        }    
    }
  }
}
@media screen and (max-width: 599px) {
  .header{
    padding: 10px 20px !important;
    .collpse_btn_prnt{
      flex: none;
    }
    .main_logo{
      margin: 0 13px;
      height: 40px;
    }
    .main_logo_prnt{
      justify-content: flex-start;
    }
  }
  .header .enterapp_prnt .enterapp_btn {
