.aboutus_main{
  margin-top: 90px;
  .about_border_box{
    position: relative;
    min-height: 200px;
    border-radius: 90px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: -1px;
      border: 1px solid transparent;
      background: linear-gradient(180deg, rgba(0, 0, 0, 1), rgba(0, 255, 115, 1)) border-box;
      -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
      mask-composite: exclude;
      -webkit-mask-composite: destination-out;
      border-radius: 76px;
      pointer-events: none;
    }
    .sadow_box{
      padding: 30px 0 35px 0;
      background: #000000;
      box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.2);
      border-radius: 90px;
      h6{
        font-family: $f_primary;
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        color: #00FF73;
      }
      h3{
        font-family: $f_primary;
        font-weight: 600;
        font-size: 42px;
        margin-top: 14px;
        line-height: 49px;
        text-align: center;
        color: #FFFFFF;
      }
    }
    .about_text_prnt{
      display: flex;
      justify-content: center;
      max-width: 1110px;
      padding: 50px 15px 85px 15px;
      margin: 0 auto;
      p{
        font-family: $f_primary;
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
        color: #FFFFFF;
        span {
          color: #00FF73;
          font-weight: bold;
        }
      }
    }
    .upcro_ic{
      position: absolute;
      bottom: -35px;
      left: calc(50% - 35px);
    }
  }
}
@media screen and (max-width: 991px) {
  .aboutus_main .about_border_box::before {
