.glow-on-hover {
  // width: 220px;
  // height: 50px;
  border: none;
  outline: none;
  // background: #000;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 60px;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(45deg, #00ffaa, #33ff00, #00ffc8, #48ff00, #00ffd5, #00fff28f, #0099ff, #008cff, #00a2ff);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 60px;
}

.glow-on-hover:active {
  color: #000
}

.glow-on-hover:active:after, .glow-on-hover:hover:after {
  background: #000;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #00FF73;
  left: 0;
  top: 0;
  border-radius: 60px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}



.glow-on-hover-v2 {
  // width: 220px;
  // height: 50px;
  border: none;
  outline: none;
  
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 60px;
}

.glow-on-hover-v2:before {
  content: '';
  background: linear-gradient(45deg, #00ffaa, #33ff00, #00ffc8, #48ff00, #00ffd5, #00fff28f, #0099ff, #008cff, #00a2ff);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 60px;
}

.glow-on-hover-v2:active {
  color: #000
}

.glow-on-hover-v2:active:after {
  background: transparent;
}

.glow-on-hover-v2:hover:before {
  opacity: 1;
}

.glow-on-hover-v2:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000;
  left: 0;
  top: 0;
  border-radius: 60px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}




.pulse {
  
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
