  0% {
    -webkit-box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
  }
  70% {
      -webkit-box-shadow: 0px 15px 80px rgba(0, 255, 115, 0);
  }
  100% {
      -webkit-box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
    box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
  }
  70% {
