      box-shadow: 0px 15px 80px rgba(0, 255, 115, 0);
  }
  100% {
      -moz-box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
      box-shadow: 0px 15px 80px rgba(0, 255, 115, 0.3);
  }
}
