.pools_main_v2{
  background: url(../img/pool_tether_bg.jpg) no-repeat center 0;
  background-size: cover;
  min-height: 100vh;
  width: 100%;
  padding: 84px 15px 40px 15px;
  .pool_list_box{
    max-width: 1490px;
    margin: 0 auto;
    opacity: 0.9;
    box-shadow: 0px 0px 10px 2px rgba(0, 255, 115, 0.4);
    border-radius: 16px;
    position: relative;
    &::before{
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: -1px;
      border: 1px solid transparent;
      background: linear-gradient(116deg, #FFE600, #00A76B) border-box !important;
      -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
      mask-composite: exclude;
      -webkit-mask-composite: destination-out;
      border-radius: 12px;
      pointer-events: none;
    }
    .flex_chart_header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px 30px;
      h3{
        font-family: $f_primary;
        font-weight: 700;
        font-size: 26px;
        line-height: 30px;
        color: #FFFFFF;
        min-width: 200px;
      }
      .search_bar{
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 547.93px;
        height: 52px;
        width: 100%;
        background: #272727;
        border-radius: 60px;
        padding: 0 12px 0 20px;
        input{
          font-family: $f_primary;
          font-weight: 300;
          font-size: 15.4075px;
          line-height: 18px;
          color: #FFFFFF !important;
          background: transparent;
          box-shadow: none;
          border: none;
          outline: none;
          width: 80%;
          &::placeholder {
            color: #FFFFFF;
          }
        }
        button{
          padding: 0;
          height: auto;
          min-width: 0;
          background: transparent;
          box-shadow: none;
          margin: 13px 13px 13px 0;
          &:hover{
            img{
              filter: invert(76%) sepia(79%) saturate(1796%) hue-rotate(86deg) brightness(100%) contrast(106%);
            }
          }
        }
      }
    }
  }
  .pagination_row {
    background: linear-gradient(180deg, #3D3D3D 0%, #181818 100%) !important;
    border-radius: 0px 0px 16px 16px;
  }
  .pools_tablemain {
    td {
      color: #FFFFFF !important;
    }
    .poolcolm_data {
      color: #FFFFFF !important;
    }
    th {
      color: #FFFFFF !important;
    }
    .new_th, .poolcolm_data_new {
      color: #BDD7FF !important;
    }
  }
}

